import React from "react"
import { Link } from "gatsby"
import { graphql } from "gatsby"
import styled from "styled-components"

import Layout from "../components/layout"
import TwoCols from "../components/layout/twoCols"
import Button from "../components/layout/button"
import Title from "../components/layout/title"
import SEO from "../components/seo"

const ProjectsList = styled.div`
  h2 {
    text-align: center;
  }

  > ul {
    padding: 0;
    list-style: none;
    max-width: 900px;
    margin: 0 auto;

    > li {
      > div {
        display: flex;
        flex-direction: column;
        padding: 1rem 1.5rem;
        border-radius: 10px;
        box-shadow: -2px 3px var(--main-color-darker);
        margin-bottom: 2rem;
        background-color: white;
      }

      h3 {
        margin-bottom: 0;
        color: var(--main-color-darker);
      }

      :before {
        content: initial;
      }
    }
  }
`

export default function ProjectsPage({ data }) {
  return (
    <Layout dimmed>
      <SEO title="Home" />
      <ProjectsList>
        <div style={{ textAlign: "center" }}>
          <Title>Projects</Title>
        </div>

        <ul>
          {data.strapi.projects.map(project => {
            const prodDev = project.product_development

            const designGuides = project.guides.filter(
              guide => guide.Category === "design"
            )
            const codeGuides = project.guides.filter(
              guide => guide.Category === "code"
            )

            return (
              <li key={project.UID}>
                <div style={{ padding: "3rem 3rem 1rem 3rem" }}>
                  <h3>{project.Title}</h3>
                  <div
                    dangerouslySetInnerHTML={{ __html: project.Description }}
                  ></div>
                  <div style={{ margin: "1rem 0 4rem", textAlign: "center" }}>
                    <Button>
                      <Link to={`/projects/${project.UID}`}>See project</Link>
                    </Button>
                  </div>

                  <div>
                    <TwoCols>
                      {designGuides.length > 0 && (
                        <div>
                          <h3>Design Research Module</h3>
                          <ul>
                            {designGuides.map((guide, key) => (
                              <li key={key}>
                                <Link to={`/guides/${guide.UID}`}>
                                  {guide.Title}
                                </Link>
                              </li>
                            ))}
                          </ul>
                        </div>
                      )}

                      {codeGuides.length > 0 && (
                        <div>
                          <h3>Code Research Module</h3>
                          <ul>
                            {codeGuides.map((guide, key) => (
                              <li key={key}>
                                <Link to={`/guides/${guide.UID}`}>
                                  {guide.Title}
                                </Link>
                              </li>
                            ))}
                          </ul>
                        </div>
                      )}
                    </TwoCols>

                    <section>
                      {prodDev && (
                        <div>
                          <h3>Product Development</h3>
                          <span>{prodDev.Title}</span>
                          <br />
                          <Link to={`/product-development/${prodDev.UID}`}>
                            Go to specification
                          </Link>
                        </div>
                      )}
                    </section>
                  </div>
                </div>
              </li>
            )
          })}
        </ul>
      </ProjectsList>
    </Layout>
  )
}

export const query = graphql`
  {
    strapi {
      projects(limit: 1000) {
        UID
        Title
        Description
        guides(limit: 1000, sort: "createdAt") {
          Title
          UID
          Skills {
            Skill
          }
          Knowledge {
            Knowledge
          }
          Category
        }
        product_development {
          Title
          UID
          Competences {
            Competence
          }
        }
      }
    }
  }
`
