import React from "react"
import styled from "styled-components"

const StyledTwoCols = styled.section`
  @media (min-width: 700px) {
    display: flex;

    > div {
      width: calc(100% / 2);
      flex-grow: 1;
    }
  }
`

export default function TwoCols({ children }) {
  return <StyledTwoCols>{children}</StyledTwoCols>
}
